.note-bb{
    display: flex;
    flex-direction: column;
    padding: 10px;

    .note-title{
        margin-bottom: 10px;
    }
    .note-body{
        margin-bottom: 10px;
    }
    .actions{
        display: flex;
        justify-content: flex-end;
        padding: 5px;

        .create{
            margin-left: 10px;
            font-size: 1rem;
            padding: 10px;
            border-radius: 10%;
            background: rgb(193, 248, 111);
        }

        .cancel{
            font-size: 1rem;
            padding: 10px;
            border-radius: 10%;
            background: rgb(245, 111, 111);
        }
    }

}